 
 <template>
  <div
    class="write-off-container"
    v-if="(iconTXT.writeOffState == 'loading')||(iconTXT.writeOffState == 'success')||(iconTXT.writeOffState == 'failure')"
  >
    <div class="write-off-content">
      <template v-if="iconTXT.writeOffState == 'loading'">
        <span class="loading iconfont icon-jiazaizhong"></span>
        <p class="write-off-TXT">{{iconTXT.TXT}}</p>
      </template>

      <template v-else-if="iconTXT.writeOffState =='success'">
        <span class="success iconfont icon-chenggong"></span>
        <p class="write-off-TXT">{{iconTXT.TXT}}</p>
      </template>

      <template v-else-if="iconTXT.writeOffState =='failure'">
        <span class="failure iconfont icon-shibai"></span>
        <p class="write-off-TXT">{{iconTXT.TXT}}</p>
      </template>

      <div class="write-off-bg"></div>
    </div>
  </div>
</template>
<script>
export default {
  props: {
    iconTXT: {
      type: Object
    }
  }
};
</script>
<style lang="less" scoped>
@keyframes rotate {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
.write-off-container {
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  z-index: 9999;
  .write-off-TXT {
    font-size: 28px;
    color: #ffffff;
    text-align: center;
    line-height: 28px;
  }
  .iconfont {
    color: #fff;
    font-size: 84px;
  }
  .loading {
    display: block;
    animation: rotate 3s linear infinite;
  }
  .write-off-content {
    position: absolute;
    top: 50%;
    left: 50%;
    width: 180px;
    height: 180px;
    transform: translate(-50%, -50%);
  }
  .write-off-bg {
    position: absolute;
    top: 0;
    left: 0;
    z-index: -1;
    width: 100%;
    height: 100%;
    background: rgba(0, 0, 0, 0.8);
    border-radius: 10px;
  }
}
</style>



