<template>
  <div class="checkPackage">
    <topNav :title="title" url="/checkIndex"></topNav>
    <searchHeader class="searchHeader" v-model="searchInput" @search-form="search"></searchHeader>
    <checkOverView :checkOverView="checkOverView"></checkOverView>
    <div class="checkPackageList">
      <ul v-if="originalData.length!=0">
        <layoutV v-for="(items,index) in originalData" :key="index" :obj="items">
          <div class="checkPackageListFooter" slot="footer">
            <mt-button
              class="checkPackageListButton repealButton"
              :class="[items.repealBtnState ? 'repealButtonTrue' : 'repealButtonFalse' ]"
              size="small"
              plain
              @click="repeal(items,index)"
            >撤销</mt-button>
            <mt-button
              class="checkPackageListButton useButtonTrue"
              size="small"
              plain
              @click="used(items,index)"
            >使用</mt-button>
          </div>
        </layoutV>
      </ul>
      <div v-else class="noValue-container">
        <template v-if="isNoBreakfast">
          <span class="noValue-icon iconfont icon-jiazaishibai"></span>
          <p class="noValue-TXT">{{noBreakfastMSG}}</p>
        </template>
      </div>
    </div>
    <mDialog
      class="dialog-checkList"
      :show="showCheckList"
      @dialog-hide="dialogCheckPackageListHanlder"
      :maskClick="true"
      :contentClick="contentClick"
      :autoHide="false"
    >
      <div slot="dialogInner">
        <div class="dialogCheckPackageList">
          <p v-if="isUsed" class="dialog-title">确认使用份数</p>
          <p v-else class="dialog-title">确认撤销份数</p>
          <div class="dialog-main">
            <span class="cut-back iconfont icon-jian" @click="cutBackFun()"></span>
            <span class="num">{{currentGuestCopiesNumber}}</span>
            <span class="add iconfont icon-jia" @click="addFun()"></span>
          </div>
          <mt-button class="dialogCheckPackageBtn" @click="confirmFun()">确认</mt-button>
        </div>
      </div>
    </mDialog>
    <loading :iconTXT="iconTXT"></loading>
    <div v-if="first">
      <img class="nothing" src="../../assets/img/nothing.png">
      <p class="nothingTxt">Ready</p>
    </div>
  </div>
</template>

<script>
import moment from "moment";
import qs from "qs";
import { Toast, Indicator, MessageBox } from "mint-ui";
import layoutV from "../common/layoutV";
import searchHeader from "../common/searchHeader";
import mDialog from "../common/Dialog";
import Icon from "../common/icon";
import checkOverView from "../common/checkOverView";
import loading from "../common/loading";
export default {
  data() {
    return {
      checkPs: {
        taCodeName: ""
      },
      contentClick: false,
      chooseIndex: 0,
      state: "pay-success-icon",
      dialogText: "核销成功",
      checkOverView: {},
      showCheckList: false,
      showTips: false,
      first: true,
      title: "早餐核销",
      currentGuestCopiesNumber: 0,
      currentGuestIndex: 0,
      bLeveal: false,
      iconTXT: {
        writeOffState: "",
        TXT: ""
      },
      style: {
        padding: "12px 14px 12px 11px",
        "box-shadow": "0 0 4px 0 rgba(0,0,0,0.1)",
        margin: "10px 0"
      },
      dialogStyle: {
        padding: "12px 14px 12px 11px",
        "box-shadow": "0 0 4px 0 rgba(0,0,0,0.1)",
        margin: "10px 0",
        border: "1px solid  #999999"
      },
      originalData: [
        // {
        //   guestName: "Yang，JinZhong ",
        //   room: 50,
        //   num: 2,
        //   isAccount: "否",
        //   used: 1,
        //   Surplus: 1,
        //   arrivalTime: "2019/1/13   17:56:52",
        //   departureTime: "2019/1/14   14:00:00"
        // }
      ],
      searchInput: "",
      noBreakfastMSG: "未查到订单",
      isNoBreakfast: false,
      adultPrice: 0,
      childPrice: 0,
      isUsed: true,
      vipStatus: ""
    };
  },
  created() {
    var ps = localStorage.getItem("checkPs");
    if (ps == null) {
      this.msg("请先设置参数");
      this.$router.push("/checkSet");
    } else {
      this.checkPs = JSON.parse(ps);
      this.adultPrice = this.checkPs.price.adultPrice;
      this.childPrice = this.checkPs.price.childPrice;
      this.title = this.title + " - " + this.checkPs.taCodeName;
    }
  },
  mounted() {
   // this.getRoomNum("64260018", "false");
    this.getBreakfastConsumeStatistics();
    this.autoGet(0);
    //this.getRoomNum("1019", "true");
   
  },

  methods: {
    autoGet(v) {
      if (this.checkPs.getType == "2") {
        this.getLockNo(v);
      }
    },
    getBreakfastConsumeStatistics() {
      this.$http
        .get("/ohotel/breakfast/fetchBreakfastConsumeStatistics")
        .then(res => {
          let rs = res.data.result;
          this.checkOverView = {
            guestsNum: rs.guestCount,
            breakfastNum: rs.totalCount ? rs.totalCount : 0,
            writeOffsNum: rs.usedCount ? rs.usedCount : 0
          };
        });
    },
    searchData(rmno, bscan) {
      let rsvDate = moment().format("YYYY-MM-DD");
      this.first = false;
      this.$http
        .get("/ohotel/breakfast/fetchValidBreakfastByRoom", {
          params: {
            condition: {
              roomNum: rmno,
              rsvDate: rsvDate
            }
          },
          dpost: true
        })
        .then(async res => {
          this.originalData = [];
          if (res.data.success) {
            if (res.data.result.resrvs.length > 0) {
              await this.getRoomNum(rmno, "true");

              res.data.result.resrvs.forEach((ele, index) => {
                let obj = {
                  guestName: ele.guestName,
                  room: ele.roomNum,
                  num: ele.breakfastCount,
                  isAccount: ele.noPostFlag == "Y" ? "是" : "否",
                  used: ele.usedBreakfastCount,
                  Surplus: ele.breakfastCount - ele.usedBreakfastCount,
                  arrivalTime: ele.checkInTime,
                  departureTime: ele.checkOutTime,
                  useBtnState:
                    ele.breakfastCount - ele.usedBreakfastCount > 0
                      ? true
                      : false,
                  repealBtnState:
                    ele.usedBreakfastCount == 0 && ele.additionalCount == 0
                      ? false
                      : true,
                  vipStatus: this.vipStatus ? this.vipStatus : "否",
                  additionalCount: ele.additionalCount,
                  postData: {
                    hotelGroupCode: ele.hotelGroupCode,
                    hotelCode: ele.hotelCode,
                    employeeCode: "1",
                    rvcCode: this.checkPs.taCode,
                    resrvNo: ele.resrvNo,
                    resrvNameId: ele.resrvNameId,
                    roomNum: ele.roomNum,
                    rsvDate:
                      moment(ele.rsvDate).format("YYYY-MM-DD") + " 00:00:00",
                    pmsHotelId: res.data.result.room.guests[index].pmsHotelId,
                    checkNum: "1",
                    seqNo: "4603",
                    checkSeqNo: "1",
                    servingTime: 1,
                    payMethod: res.data.result.room.guests[index].payMethod,
                    creditLimitOverride: "false",
                    details: [
                      {
                        count: ele.breakfastCount,
                        usedCount: ele.usedBreakfastCount,
                        resrvNo: ele.resrvNo,
                        resrvNameId: ele.resrvNameId,
                        guestNum: ele.guestNum,
                        guestName: ele.guestName,
                        fullName: ele.guestName,
                        adultCount: 0,
                        childCount: 0,
                        adultPrice: this.adultPrice,
                        childPrice: this.childPrice
                      }
                    ]
                  }
                };
                this.originalData.push(obj);
              });
              if (bscan && this.checkPs.verification == "2") {
                let hasBreakfast = false;
                for (var i = 0; i < this.originalData.length; i++) {
                  if (this.originalData[i].Surplus > 0) {
                    hasBreakfast = true;
                    this.currentGuestCopiesNumber = 1;
                    this.isUsed = true;
                    this.currentGuestIndex = i;
                    this.confirmFun();
                    break;
                  }
                }
                if (hasBreakfast == false) {
                  try {
                    playSound(0);
                  } catch (error) {}
                }
              }
            } else {
              this.isNoBreakfast = true;
              this.noBreakfastMSG = "未查到数据";
            }
          } else {
            this.isNoBreakfast = true;
            this.noBreakfastMSG = res.data.msgs[0];
            try {
              playSound(0);
            } catch (error) {}
          }
        });
    },

    getLockNo(v) {
      if (!this.bLeveal) {
        var ps = {
          secNum: this.checkPs.secNum,
          time: v
        };
        rfcRoom(this.rfSuccess, this.rfError, ps);
      }
    },
    rfSuccess(v) {
      console.log(v);
      if (v == "0") {
        return;
      }
      this.autoGet(this.checkPs.time);
      if (!v) {
        return;
      }
      if (v.length > 4) {
        //alert(v);
        this.getRoomNum(v, "false");
      } else {
        this.searchInput = v;
        this.searchData(v, true);
      }
    },
    rfError(v) {
      this.msg(v);
      this.autoGet(this.checkPs.time);
    },
    dialogCheckPackageListHanlder() {
      this.showCheckList = false;
    },
    async getRoomNum(str, checkVip) {
      let params;
      if (checkVip == "true") {
        params = {
          condition: {
            roomNum: str
          }
        };
      } else {
        params = {
          condition: {
            resrvNum: str
          }
        };
      }
      await this.$http
        .get("/ohotel/generic/guestInfo", {
          params: params,
          dpost: true
        })
        .then(res => {
          if(res.data.success){
            if (checkVip == "true") {
              if(res.data.result.length > 0){
                this.vipStatus = res.data.result[0].vipStatus;
              }
              // console.log(" this.vipStatus:L", this.vipStatus);
            } else {
              if (res.data.result.length > 0) {
                this.searchInput = res.data.result[0].rmno;
                this.searchData(res.data.result[0].rmno, true);
              } else {
                this.isNoBreakfast = true;
                this.searchInput="";
                this.noBreakfastMSG = "未查到数据";
                this.originalData = [];
                this.first = false;
                playSound(0);
                this.msg("找不到对应房号数据");
              }
            }
          }

        });
    },
    msg(msg) {
      Toast({
        message: msg,
        position: "bottom",
        duration: 3000
      });
    },
    msg2(msg) {
      MessageBox.alert(msg);
    },
    search(v) {
      if (!v) {
        this.msg("请输入房号");
        return;
      }
      this.originalData = [];
      this.searchData(v, false);
    },
    repeal(item, index) {
      if (!item.repealBtnState) {
        return;
      }
      this.currentGuestCopiesNumber = item.used + item.additionalCount;
      this.currentGuestIndex = index;
      this.showCheckList = true;
      this.isUsed = false;
    },
    used(item, index) {
      // if (!item.useBtnState) {
      //     return;
      // }
      if (item.Surplus == 0) {
        this.currentGuestCopiesNumber = 1;
      } else {
        this.currentGuestCopiesNumber = item.Surplus;
      }

      this.currentGuestIndex = index;
      this.showCheckList = true;
      this.isUsed = true;
    },
    addFun() {
      if (this.isUsed) {
        // if (
        //     this.currentGuestCopiesNumber >=
        //     this.originalData[this.currentGuestIndex].Surplus
        // ) {
        //     return;
        // } else {
        this.currentGuestCopiesNumber++;
        // }
      } else {
        if (
          this.currentGuestCopiesNumber >=
          this.originalData[this.currentGuestIndex].used +
            this.originalData[this.currentGuestIndex].additionalCount
        ) {
          return;
        } else {
          this.currentGuestCopiesNumber++;
        }
      }
    },
    cutBackFun() {
      if (this.currentGuestCopiesNumber <= 1) {
        return;
      } else {
        this.currentGuestCopiesNumber--;
      }
    },
    confirmFun() {
      this.showCheckList = false;
      if (
        this.isUsed &&
        this.originalData[this.currentGuestIndex].Surplus <
          this.currentGuestCopiesNumber
      ) {
        MessageBox({
          title: "提示",
          message: "超过剩余数量，是否继续使用",
          showCancelButton: true,
          closeOnClickModal: false,
          confirmButtonText: "确定"
        }).then(action => {
          if (action == "confirm") {
            this.postFun();
          }
        });
      } else {
        this.postFun();
      }
    },
    postFun() {
      if (this.isUsed) {
        this.originalData[
          this.currentGuestIndex
        ].postData.details[0].adultCount = this.currentGuestCopiesNumber;
      } else {
        this.originalData[
          this.currentGuestIndex
        ].postData.details[0].adultCount = -this.currentGuestCopiesNumber;
      }

      this.$http
        .post(
          "/ohotel/breakfast/consumeBreakfast",
          `source=${JSON.stringify(
            this.originalData[this.currentGuestIndex].postData
          )}`
        )
        .then(res => {
          if (res.data.success) {
            let u = this.originalData[this.currentGuestIndex];
            if (this.isUsed) {
              if (this.currentGuestCopiesNumber > u.num - u.used) {
                u.additionalCount +=
                  this.currentGuestCopiesNumber - (u.num - u.used);
                this.checkOverView.writeOffsNum += u.num - u.used;
                u.used = u.num;
                u.Surplus = 0;
              } else {
                u.used = u.used + this.currentGuestCopiesNumber;
                u.Surplus = u.Surplus - this.currentGuestCopiesNumber;
                this.checkOverView.writeOffsNum =
                  this.checkOverView.writeOffsNum +
                  this.currentGuestCopiesNumber;
              }
            } else {
              if (this.currentGuestCopiesNumber <= u.additionalCount) {
                u.additionalCount -= this.currentGuestCopiesNumber;
              } else {
                u.used =
                  u.used - (this.currentGuestCopiesNumber - u.additionalCount);
                u.Surplus =
                  u.Surplus +
                  (this.currentGuestCopiesNumber - u.additionalCount);

                this.checkOverView.writeOffsNum =
                  this.checkOverView.writeOffsNum -
                  (this.currentGuestCopiesNumber - u.additionalCount);
                u.additionalCount = 0;
              }
            }

            if (u.Surplus == 0) {
              u.useBtnState = false;
            } else {
              u.useBtnState = true;
            }
            if (u.used > 0 || u.additionalCount > 0) {
              u.repealBtnState = true;
            } else {
              u.repealBtnState = false;
            }

            try {
              playSound(1);
            } catch (error) {}
          } else {
            try {
              playSound(0);
            } catch (error) {}

            this.msg("处理失败，请重试");
          }
        });
    },
    SwipeFun() {
      this.getLockNo(0);
    }
  },
  components: {
    // layout,
    layoutV,
    searchHeader,
    mDialog,
    checkOverView,
    Icon,
    loading
  },
  beforeRouteLeave(to, from, next) {
    this.bLeveal = true;
    rfcRoomStop();
    next();
  }
};
</script>

<style lang="less" scoped>
.checkPackage {
  height: 100vh;
  overflow: auto;
  background: #f4f5f7;

  .searchHeader {
    margin-bottom: 15px;
  }

  .checkPackageTitle {
    h1 {
      padding: 11px 0;
      font-family: PingFangSC-Regular;
      font-size: 14px;
      color: #333333;
    }
  }

  .checkPackageSearch {
    display: flex;
    justify-content: space-around;
    align-items: center;

    .icon-shuaqia {
      font-size: 28px;
      padding: 0 9px;
      width: 46px;
    }

    .checkPackageSearchInput {
      position: relative;
      width: 100%;
      height: 30px;

      input {
        box-sizing: border-box;
        width: 100%;
        border: 1px solid #cccccc;
        padding: 0 9px;
        border-radius: 5px;
        position: absolute;
        top: 0;
        left: 0;
        bottom: 0;
        right: 0;
        outline: none;
      }
    }

    .searchBtnBox {
      width: 99px;
      height: 30px;
      padding: 0 9px 0 16px;

      .searchBtn {
        width: 100%;
        height: 100%;
        border: none;
        color: #fff;
        background: #f9c203;
        background: #d8af68;
        border-radius: 7px;
        padding: 0;
        margin: 0;
        outline: none;
      }
    }
  }

  .checkPackageList {
    margin: 10px 8px 0;

    .checkPackageListFooter {
      padding: 8px 0 2px;
      text-align: right;
      margin-top: 16px;
      border-top: 1px solid #eeeeee;
    }

    .checkPackageListButton {
      width: 70px;
      height: 30px;
      border-radius: 7px;
      border: none;
      color: #fff;
      margin-left: 10px;
    }

    .repealButton {
      background: #d9d9d9;
    }

    .throwAccountButton {
      background: #d8af68;
    }

    .useButton {
      background: #ff8902;
    }

    .useButtonTrue,
    .repealButtonTrue {
      background: #ff8902;
    }

    .useButtonFalse,
    .repealButtonFalse {
      background: #d9d9d9;
    }

    .noValue-container {
      margin-top: 35%;

      .noValue-icon {
        font-size: 2.5rem;
      }

      .noValue-TXT {
        font-size: 0.4rem;
        color: #333333;
      }
    }
  }

  .icon {
    width: 64px;
    height: 64px;
    margin: 103px auto 20px;
  }

  .tips-text {
    font-family: PingFangSC-Regular;
    font-size: 20px;
    color: #333333;
  }

  .dialogCheckPackageList {
    margin: 18px 18px 23px;

    // ul {
    //   height: 250px;
    //   overflow: auto;
    //   li {
    //     margin: 10px 0;
    //     .choosed {
    //       border: 1px solid #d8af68 !important;
    //     }
    //   }
    // }
    .dialog-title {
      font-size: 14px;
      color: #666666;
      text-align: center;
      line-height: 15px;
    }

    .dialog-main {
      display: flex;
      align-items: center;
      justify-content: space-between;
      border-bottom: 1px solid #eeeeee;

      .num {
        font-size: 36px;
        color: #000000;
      }

      .iconfont {
        font-size: 28px;
        color: #ff8902;
      }
    }

    .dialogCheckPackageBtn {
      width: 135px;
      height: 37px;
      margin-top: 22px;
      background: #ff8902;
      border-radius: 7px;
      color: #fff;
    }

    .mint-button {
      font-size: 14px;
    }
  }

  .nothing {
    width: 80px;
    height: 88px;
    margin: 50px auto 0 auto;
  }

  .nothingTxt {
    text-align: center;
    font-size: 20px;
    color: #ccd1d5;
    margin-top: 20px;
  }
}
</style><style lang="less">
.checkPackage .dialog .dialog-content {
  width: 68% !important;
  height: auto !important;
}
</style>



