<template>
  <div class="dialog" v-show="show">
    <div class="mask" @click="maskClickHandler"></div>
    <div class="dialog-content" @click="contentClickHanlder">
      <slot name="dialogInner"></slot>
    </div>
  </div>
  <!-- -->
</template>
<script>
// import Icon from "./icon";
export default {
  props: {
    autoHide: {
      type: Boolean,
      default: true
    },
    interval: {
      type: Number,
      default: 3000
    },
    maskClick: {
      type: Boolean,
      default: false
    },
    show: {
      type: Boolean,
      default: false
    },
    contentClick: {
      type: Boolean,
      default: true
    }
    // state: {
    //   type: String,
    //   default: "success"
    // },
    // text: {
    //   type: String,
    //   default: ""
    // }
  },
  components: {
    // Icon
  },
  data() {
    return {
      timer: null
      // config: {
      //   success: "成功",
      //   warning: "警告",
      //   error: "错误"
      // }
    };
  },
  computed: {
    // tipText() {
    //   if (!this.text) {
    //     return this.config[this.state];
    //   } else {
    //     return this.text;
    //   }
    // }
  },
  watch: {
    show(val) {
      if (val) {
        if (this.autoHide) {
          // clearTimeout(this.timer);
          // this.timer = setTimeout(() => {
          //   this.hideDialog();
          // }, this.interval);
        }
      }
    }
  },
  methods: {
    maskClickHandler() {
      if (this.maskClick) {
        this.hideDialog();
      }
    },
    contentClickHanlder() {
      if (this.contentClick) {
        this.hideDialog();
      }
    },
    hideDialog() {
      clearTimeout(this.timer);
      this.$emit("dialog-hide", false);
    },
    showDialog() {
      this.$emit("dialog-show", true);
    }
  }
};
</script>
<style lang="less" scoped>
.dialog {
  position: fixed;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  .mask {
    position: fixed;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    background: rgba(0, 0, 0, 0.3);
  }
  .dialog-content {
    position: absolute;
    width: 82.6667%;
    height: 350px;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
    border-radius: 5px;
    background: #ffffff;
  }
}
</style>




